import React from "react";
import styled from "styled-components";
import { Search } from "@styled-icons/material-sharp/Search";
import { Icon } from "../../0-base/Icon";
import { BoxContentCenter } from "../../0-base/Box";

const StyledContainer = styled(BoxContentCenter)`
  /* display: flex; */
  border-radius: 4px;
  background-color: rgba(${(props) => props.theme.color.blueberry});

  /* &:hover {
    background-color: rgba(255, 255, 255, 0.25);
  } */
`;

const StyledSearchBox = styled.input.attrs({
  placeholder: "Search...",
})`
  width: 141px;
  height: 16px;
  padding: 8px;

  color: ${(props) => props.theme.color.cultured};

  transition: width 300ms ease-in;

  &::placeholder {
    color: rgb(${(props) => props.theme.color.jordyBlue});
  }

  &:focus {
    width: 225px;
  }
`;

const StyledIconContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
`;

const StyledSearchIcon = styled(Icon).attrs({
  as: Search,
})``;

export default function SearchBox(): JSX.Element {
  return (
    <StyledContainer>
      <StyledSearchIcon />
      <StyledSearchBox />
    </StyledContainer>
  );
}
